/* Reset + base */
*{box-sizing:border-box}
html,body,#root{height:100%}
body{font-family:Inter, ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial; margin:0; background:#0f1724; color:#e6eef8}
a{color:inherit}
.container{max-width:1100px;margin:0 auto;padding:20px}


/* Utility */
.row{display:flex;flex-wrap:wrap;gap:16px}
.card{background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); border:1px solid rgba(255,255,255,0.04); padding:16px; border-radius:12px}


/* Responsive text */
h1,h2,h3{margin:0 0 12px 0}


/* Small screens adjustments handled in component CSS */


/* Link button */
.btn{display:inline-flex;align-items:center;gap:8px;padding:8px 12px;border-radius:8px;border:1px solid rgba(255,255,255,0.06);background:transparent;text-decoration:none}


/* Footer small */
.footer{opacity:0.7;font-size:0.9rem}

/* Theme modes */
.dark-theme {
  background-color: #0b1220;
  color: #e6eef8;
}

.light-theme {
  background-color: #f7fbff;
  color: #0b1220;
}

/* Smooth transition */
body, html {
  transition: background-color 0.3s ease, color 0.3s ease;
}
