.app{min-height:100vh;display:flex;flex-direction:column}
main.container{flex:1;padding-top:28px;padding-bottom:28px}


/* Section layout */
.section-grid{display:grid;grid-template-columns:1fr 320px;gap:20px}
@media (max-width:880px){.section-grid{grid-template-columns:1fr}}


/* Project cards */
.projects-list{display:grid;grid-template-columns:repeat(2,1fr);gap:16px}
@media (max-width:880px){.projects-list{grid-template-columns:1fr}}


.skill-badges{display:flex;flex-wrap:wrap;gap:8px}
.skill-badge{padding:8px 10px;border-radius:999px;border:1px solid rgba(255,255,255,0.06);font-weight:600}


.header-actions{display:flex;gap:8px;align-items:center}


/* subtle card hover */
.card:hover{transform:translateY(-4px);transition:transform 180ms ease}


/* link icons */
.icon{width:18px;height:18px;display:inline-block}